 
.center {
  margin: auto;
  width: 50%;
  border: 3px solid green;
  padding: 10px;
}

#overlay {
  position: fixed; 
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.6);
  z-index: 2; 
  
}

#float-box {
  width: 220px;
  color: #000;
  padding: 15px;   
  border-radius: 6px;
  background-color: white;
  text-align: center;
  margin: 150px auto; 
}

.modal {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.4);
  z-index: 2; 
}

.modal-content {
  width: 250px;
  color: #000;
  padding: 5px;   
  border-radius: 6px;
  background-color: white;
  text-align: center;
  margin: 10% auto;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

input {
  width: 100%;  
  height: 24px; 
  padding-left: 5px; 
  outline: none; 
  border: 1px solid #4267B2;  
  border-radius: 4px;
}

button {
  width: 100%; 
  margin-top: 5px; 
  border: 1px solid #4267B2;  
  padding: 5px; 
  background-color: #4267B2; 
  color: white; 
  font-size: 15px; 
  border-radius: 4px; 
  cursor: pointer;
}

.spinners {
  width: 50px;
  height: 50px;
  border: 5px solid #f3f3f3;
  border-top: 5px solid #3498db;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 150px auto;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

#login-box {
  display: none; 
}







